.table-bank-accounts .actions-bank-account {
  cursor: pointer;
}
.table-bank-accounts .actions-bank-account .edit-bank-account,
.table-bank-accounts .actions-bank-account .remove-bank-account {
  opacity: 0;
}
.table-bank-accounts tr:hover {
  cursor: pointer;
}
.table-bank-accounts tr:hover .actions-bank-account .edit-bank-account,
.table-bank-accounts tr:hover .actions-bank-account .remove-bank-account {
  opacity: 0.5;
}
.table-bank-accounts tr:hover .actions-bank-account .edit-bank-account:hover,
.table-bank-accounts tr:hover .actions-bank-account .remove-bank-account:hover {
  opacity: 1;
}
.table-bank-accounts .edit-bank-account {
  padding: 10px;
}
.table-bank-accounts .edit-bank-account svg {
  color: #0053FA;
}
.table-bank-accounts .remove-bank-account {
  padding: 10px;
}
.table-bank-accounts .remove-bank-account svg {
  color: #F06879;
}
.mandate .row-title {
  display: block;
  font-family: 700;
  font-size: 10px;
  line-height: 10px;
  color: #8a8a8a;
  letter-spacing: 0;
  margin-bottom: 10px;
}
.mandate .row-detail {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #0d1b2a;
  letter-spacing: 0;
  margin-bottom: 15px;
}
.mandate a {
  color: #0053FA;
  font-family: 700;
  font-size: 11px;
  letter-spacing: 0;
  text-transform: uppercase;
  text-decoration: underline;
}
.mandate .row-mandate-explain {
  margin-top: 21px;
  font-weight: 400;
  font-size: 10px;
  color: #0d1b2a;
}
